<template>
  <div class="hello">
    <h3>nRoute Route Editor: {{ routeInfo.host.join(', ') }}</h3>
    <label>Hosts</label>
    <div class="input-group mb-3" v-for="(host, index) in routeInfo.host" :key="'host_' + index">
      <input type="text" class="form-control" placeholder="Host" v-model="routeInfo.host[index]" />
      <div class="input-group-prepend" v-if="routeInfo.host.length - 1 == index">
        <button type="button" class="remove-field btn btn-danger" v-on:click="removeHost">Remove</button>
      </div>
    </div>
    <button type="button" class="add-field btn btn-success" v-on:click="addHost">Add host</button>
    <br>
    <label>Target</label>
    <div class="input-group mb-3" v-for="(target, index) in routeInfo.target" :key="'target_' + index">
      <input type="text" class="form-control" placeholder="Target" v-model="routeInfo.target[index]" />

      <div class="input-group-prepend" v-if="routeInfo.target.length - 1 == index">
        <button type="button" class="remove-field btn btn-danger" v-on:click="removeTarget">Remove</button>
      </div>
    </div>

    <button type="button" class="add-field btn btn-success" v-on:click="addTarget">Add target</button>
    <br>

    <br>
    <div class="form-check">
      <input class="form-check-input" type="checkbox" value="" id="defaultCheck1" v-model="routeInfo.websocket">
      <label class="form-check-label" for="defaultCheck1">
        Proxy WebSocket
      </label>
    </div>
    <div class="form-check">
      <input class="form-check-input" type="checkbox" value="" id="defaultCheck2" v-model="routeInfo.lockDetails">
      <label class="form-check-label" for="defaultCheck2">
        Hide route info from /cdn-cgi/trace
      </label>
    </div>
    <div class="form-check">
      <input class="form-check-input" type="checkbox" value="" id="defaultCheck3" v-model="https">
      <label class="form-check-label" for="defaultCheck3">
        Use HTTPS
      </label>
    </div>

    <div class="httpsSettings" v-if="https">
      <label for="httpsCertificate">HTTPS Certificate</label>
      <textarea class="form-control" id="httpsCertificate" v-model="routeInfo.secure.cert"></textarea>

      <label for="httpsKey">HTTPS Private Key</label>
      <textarea class="form-control" id="httpsKey" v-model="routeInfo.secure.key"></textarea>

      <br>
      <button v-on:click="letsEncrypt" class="btn btn-success">Generate Let's Encrypt Certificate</button>
    </div>
    <br>
    <button v-on:click="save" class="btn btn-success">Save route</button>
    <router-link to="/" tag="button" class="btn btn-warning">Go back</router-link>
    <button v-on:click="removeRoute" class="btn btn-danger" v-if="this.$route.params.routeId !== 'new'">Remove route</button>

  </div>
</template>

<script>
  function makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 5; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }

  import axios from 'axios'

  export default {
    name: 'HelloWorld',
    data() {
      return {
        routeInfo: {},
        https: false
      }
    },
    beforeMount() {
      if (this.$route.params.routeId == 'new') {
        this.routeInfo = {
          'host': [],
          'target': [],
          'secure': false,
          'websocket': false,
          'lockDetails': false
        }
      } else {
        axios.get('/routes/' + this.$route.params.routeId).then((result) => {
          console.log(result)
          this.routeInfo = result.data
          if (result.data.https != false) {
            this.https = true
          }
        })
      }


    },

    methods: {
      removeRoute: function () {
        $.notify({
          // options
          message: 'Removing route..'
        }, {
          // settings
          type: 'success'
        });
        axios.delete('/routes/' + this.$route.params.routeId)
          .then((response) => {
            $.notify({
              // options
              message: 'Route has been successfully removed.'
            }, {
              // settings
              type: 'success'
            });
            this.$router.push('/')
          })
      },
      removeHost: function () {
        this.routeInfo.host.pop()
      },

      addHost: function () {
        this.routeInfo.host.push(makeid() + '.invalid')
      },
      removeTarget: function () {
        this.routeInfo.target.pop()
      },

      addTarget: function () {
        this.routeInfo.target.push('http://' + makeid() + '.invalid')
      },
      save: function () {
        $.notify({
          // options
          message: 'Updating Route..'
        }, {
          // settings
          type: 'info'
        });

        let newRoute = {
          'host': this.routeInfo.host,
          'target': this.routeInfo.target,
          'secure': this.https ? this.routeInfo.secure : false,
          'websocket': this.routeInfo.websocket,
          'lockDetails': this.routeInfo.lockDetails
        }
        let request
        if (this.$route.params.routeId == 'new') {
          request = axios.post('/routes', newRoute)
        } else {
          request = axios.patch('/routes/' + this.$route.params.routeId, newRoute)
        }
        let isNew=this.$route.params.routeId == 'new'
        let router=this.$router
        request.then(function (response) {
            if (isNew) {

            $.notify({
              // options
              message: 'Route has been successfully created.'
            }, {
              // settings
              type: 'success'
            });
            router.push(response.headers.location.replace('/routes/', '/routesEditor/'))
            } else {

            $.notify({
              // options
              message: 'Route has been successfully updated.'
            }, {
              // settings
              type: 'success'
            });
            }
          })
          .catch(function (error) {
            console.log(error)
            if (error.response.data == 'Conflict') {
              $.notify({
                // options
                message: '<strong>Host is conflicting with another route.</strong>'
              }, {
                // settings
                type: 'danger'
              });
            } else {
              $.notify({
                // options
                message: '<strong>There was a problem to save route.</strong>'
              }, {
                // settings
                type: 'danger'
              });
            }

          });

      },
      letsEncrypt: function (event) {


        $.notify({
          // options
          message: 'Generating certificate...'
        }, {
          // settings
          type: 'info'
        });

        axios.get('http://lvh.me:8909/certificate/letsencrypt/' + this.routeInfo.host.join(',')).then((result) => {
          console.log(result.data)
          $.notify({
            // options
            message: 'Certificate has been successfully updated using Let\'s Encrypt!'
          }, {
            // settings
            type: 'success'
          });
          this.routeInfo.secure = {
            cert: result.data.certs.cert,
            key: result.data.certs.privkey
          }
        }).catch((result) => {
          console.log(result)
          $.notify({
            // options
            message: '<strong>There was a problem to generate certificate using Let\'s Encrypt!.</strong><br>Error Code: ' +
              result.response.data.error.code
          }, {
            // settings
            type: 'danger'
          });
        })

      },
    },
    mounted() {

    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

</style>



