<template>
  <div class="hello">
    <h1>nRoute Dashboard</h1>
    <div class="list-group">
  <router-link v-for="item in items" :key="item.id" :to="'/routesEditor/' + item.id" class="list-group-item list-group-item-action">{{ item.host }}</router-link>
    <router-link :to="'/routesEditor/new'" class="list-group-item list-group-item-action">Create new route</router-link></div>
  
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'HelloWorld',
  data () {
    return {
      items: []
    }
  },
      beforeMount() {
          console.log(this.items)
      axios.get('/routes').then((result) => {
          Object.keys(result.data.data).forEach((uuid) => {
            let newData = result.data.data[uuid]
            newData.host = newData.host.join(', ')
            newData.id = uuid
            this.items.push(newData)
          })
      })
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



